.close-register-modal {
  position: absolute;
  top: -1%;
  right: -2%;
}

.register-modal-main {
  /* display: flex;
    flex-direction: column; */
  margin: 4.9rem 6rem;
}

.register-modal-title {
  font: normal normal 500 1.4rem Poppins-Medium;
  color: #202020;
}

.register-modal-heading {
  font: normal normal 500 4.1rem Poppins-Medium;
  color: #202020;
  margin-top: 0.4rem;
  margin-bottom: 3.8rem;
}
.register-modal-allinputs-container {
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}
.register-modal-main input {
  background: #ffffff 0% 0% no-repeat padding-box;
  border: none;
  outline: none;
  border: 1px solid #dcdbdd;
  border-radius: 1.2rem;
  padding-left: 2rem;
  font: normal normal normal 1.4rem Poppins-Regular;
}
.register-modal-main input::placeholder {
  font: normal normal normal 1.4rem Poppins-Regular;
  width: 100%;
}

.register-email-input {
  width: 39.3rem;
  height: 5.5rem;
}

.register-name-input-div {
  width: 100%;
  display: flex;
  gap: 1.3rem;
}
.register-name-input-div > input {
  width: 18.9rem;
  height: 4.8rem;
}

.register-password-input-div,
.register-phoneNum-input-div {
  width: 39.3rem;
  height: 5.5rem;
  position: relative;
  display: flex;
  align-items: center;
}
.register-pswrd-input {
  width: 100%;
  height: 100%;
}
.pswrd-eye-button-css {
  position: absolute;
  right: 4%;
  font-size: 2rem;
  color: #84818a;
  cursor: pointer;
}

.register-gender-select-div {
  display: flex;
  align-items: center;
}
.register-gender-select-div > span {
  display: flex;
  align-items: center;
  gap: 1rem;
  font: normal normal normal 1.4rem Poppins-Regular;
  color: #84818a;
  padding-left: 1rem;
}
.register-gender-select-div > span:nth-of-type(1) {
  margin-right: 5.5rem;
}
#registerOptionFemale,
#registerOptionMale {
  width: 2rem;
  height: 2rem;
  border: 1px solid #212020;
  border-radius: 50%;
}
.register-gender-select-div > span:nth-of-type(2) {
  margin-right: 3rem;
}
/* hiding input type number spin buttons*/
.register-phoneNum-input-div input::-webkit-outer-spin-button,
.register-phoneNum-input-div input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.register-phoneNum-input-div input[type="number"] {
  -moz-appearance: textfield;
}

.register-privacyterms-div {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.register-privacyterms-div > input {
  width: 3rem;
  height: 3rem;
}
.register-privacyterms-div > span {
  color: #84818a;
  font: normal normal normal 1.2rem Poppins-Regular;
}

.registermodal-otp-btn {
  width: 39.3rem;
  height: 4.8rem;
  background: #1c1c1c 0% 0% no-repeat padding-box;
  border: none;
  outline: none;
  border-radius: 1.2rem;

  font: normal normal 500 1.4rem Poppins-Medium;
  color: #ffffff;

  margin-top: 2.7rem;
}

/************************/
.register-divider {
  display: flex;
  margin-top: 3.8rem;
}

.register-left-div,
.register-right-div {
  width: 20rem;
  margin-top: 1rem;
  border-top: 1px solid #dcdbdd;
}

.register-or {
  font: normal normal Normal 1.4rem Poppins-Regular;
  margin-inline: 1rem;
}

.register-other-Options {
  display: flex;
  column-gap: 1.57rem;
  margin-top: 2.97rem;
}

.register-google-logo-conatiner,
.register-facebook-logo-conatiner {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 2rem;
  width: 20.3rem;
  height: 5.2rem;
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 1px solid #dcdbdd;
  border-radius: 1.2rem;
}

.register-signin-vai-google,
.register-signin-vai-facebook {
  font: normal normal 500 1.4rem Poppins-Medium;
  color: #202020;
}

.register-other-Options img {
  width: 1.7rem;
  height: 1.7rem;
}
