
.payment-main{
    margin-top:7.7rem ;
    margin-bottom: 44rem;
}
.payment-container {
    display: flex;
    flex-direction: column;
    margin-inline: auto;
    border: 1px solid black;
   
}

.show-checkout-status {
    display: flex;
    margin-inline: auto;
    justify-content: center;
    align-items: center;
    margin-bottom: 5rem;
}

.status-border {
    width: 4.1rem;
    height: 0px;
    border: 1px solid #2C2B2B;
    margin-block: 7.7rem;
}



.numbers {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2.8rem;
    height: 2.8rem;
    border-radius: 50%;
    background-color: #000000;

    font: normal normal 500 1.6rem Poppins-Medium;
    color: #FFFFFF;
    margin-right: 1.6rem;
    margin-left: 1.6rem;
}
