.status {
    font: normal normal normal 1.6rem Poppins-Regular;
    color: #000000;
}

.pending-status {
    color: #CECECE;
}

.payment-content {
    display: flex;
    flex-direction: column;
    justify-content: center !important;
    align-items: center !important;
    gap: 4rem;
    margin: 0 auto;
}

.payment-heading {
    align-self: start;
    font: normal normal bold 3rem/3rem Urbanist-Bold;
    color: #1D1D1D;
    margin-left: 17rem;
}

.payment-box {
    display: grid;
    grid-template-columns: 1fr 4fr;
    place-content: center;
    place-items: center;
}

.payment-options {
    display: flex;
    flex-direction: column;
    gap: 3.6rem;
}

.individual-methods {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 2.5rem;
    margin-bottom: 4rem;
    margin-left: 3rem;
}

input[type="radio"] {
    appearance: none;
    background-color: #fff;
    width: 2.8rem;
    height: 2.8rem;
    border: 1px solid #707070;
    border-radius: 50%;
}

input[type="radio"]:hover {
    background-color: rgb(218, 218, 218);
}

input[type="radio"]:checked {
    background-color: black;
}

.method-icon {
    width: 6.8rem;
    height: 6.5rem;
    background: #FFFFFF;
    border: 1px solid #707070;
    border-radius: 0.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.method-icon img {
    width: 4rem;
    height: 4rem;
    object-fit: scale-down;
}

.payment-method-name {
    font: normal normal 500 2rem/2.6rem Rubik-Medium;
    color: #141414;
}

@media(max-width:600px){
    .payment-heading{
       margin-inline:auto;
       margin-bottom: 4rem;
    }
    .payment-box {
        grid-template-columns: 1fr;
    }
    .payment-options{
        display: grid;
        grid-template-columns: 1fr 1fr;
        place-content: start;
        place-items: start;
        margin-bottom: 10rem;
    }
}

@media(max-width:500px){
    .payment-options{
        margin-left: 3rem;
        column-gap: 0.1rem;
    }
    .numbers{
        font-size: 2.5rem;
        width: 3.5rem;
        height: 3.5rem;
    }
    .status{
        font-size: 2.5rem;
        letter-spacing: 0.5px;
    }
    .payment-heading{
        font-size: 4rem;
    }
    .payment-method-name{
        font-size: 2.9rem;
    }
}